<template>
<div>
<br/>
<br/>
 <div class="container ">
<div class="custom-container">
<br/>

<base href="https://s3-us-west-2.amazonaws.com/s.cdpn.io/4273/" >
<div id="slider">
<div class="gallery">
<figure>
<img src="data:image/jpeg;base64," alt>
<img src="https://goo.gl/images/m6rnte" height="600" width="600"alt>
<img src="data:image/jpeg;base64," alt>
<img src="https://goo.gl/images/m6rnte" height="600" width="600"alt>
<img src="data:image/jpeg;base64," alt>
<img src="https://goo.gl/images/m6rnte" height="600" width="600"alt>

</figure>
</div>
</div>
 </div>
 </div>
 </div>
</template>

<script>
import axios from 'axios';
import Loader from './Loader';
import Chart from 'chart.js';



export default {
  //props: ['quizData'],
  components:{
    Loader
  },
  data(){
      return{
		  title: '',
           slides: 6,
           slide: 0,
           sliding: null,
          gamesList: "",
          game: "",
          result: [],
          timer: '',
          counter: '',
          showLoader: false,
		
      }
  }
}
</script>

<style scoped>
@import url(https://fonts.googleapis.com/css?family=Anaheim);
body{
  background: -webkit-linear-gradient(top,#e3e3e3,white,#e3e3e3);
}

@keyframes slidy {
0% { left: 0%; }
20% { left: 0%; }
25% { left: -100%; }
45% { left: -100%; }
50% { left: -200%; }
70% { left: -200%; }
75% { left: -300%; }
95% { left: -300%; }
100% { left: -400%; }
}

body { margin: 0; } 
div#slider { overflow: hidden; }
div#slider figure img { width: 20%; float: left; }
div#slider figure { 
  position: relative;
  width: 500%;
  margin: 0;
  left: 0;
  text-align: left;
  font-size: 0;
  animation: 20s slidy infinite; 
}
img{
      
      width: 100%;
      height:500px;
      margin: auto;
      opacity:0.90;
}

</style>



